@import url('https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300|Open+Sans:300,400,600,800|Oswald:300,400');
.primaryBold {
  font-family: 'Open Sans', sans-serif;
  font-size: 2rem;
  font-weight: 600;
  text-transform: uppercase;
}
.gridGeneral {
  position: relative;
  display: flex;
  overflow: hidden;
  box-sizing: border-box;
}
.sf-expand {
  flex-grow: 1;
}
.sf-container {
  position: relative;
  display: flex;
  overflow: hidden;
  box-sizing: border-box;
}
.sf-container-stack {
  position: relative;
  display: flex;
  overflow: hidden;
  box-sizing: border-box;
  flex-direction: column;
}
.white-bg {
  background: #fff !important;
}
.white {
  color: #fff;
}
@font-face {
  font-family: 'Century Gothic';
  src: url("../fonts/centuryGothic.ttf");
}
@font-face {
  font-family: 'Segoe';
  src: url("../fonts/SegoeUI.ttf");
}
.section-general {
  max-height: 100%;
  width: auto;
}
.hero-section {
  max-height: 100%;
  width: auto;
}
.section {
  max-height: 100%;
  width: auto;
}
.info-section {
  background: #343434;
}
.info-section {
  background: #343434;
  color: #FFF;
  text-align: center;
  height: unset;
  max-height: unset;
}
.info-section p {
  margin: 0;
  margin-top: .75%;
}
.info-section-bottom-pad {
  padding-bottom: 2.8%;
}
.hero-title,
.info-section > .sub-title {
  font-family: 'Century Gothic';
  font-size: 2.5rem;
  text-transform: uppercase;
}
.info-section > .sub-text {
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  font-size: 1.3rem;
}
div.info-button,
.info-section > div.info-button {
  margin-top: 1.5%;
  border: 1px solid #343434;
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  color: #FFF;
  border: 1px solid #FFF;
  padding: 20px 50px;
  align-self: center;
  font-size: 1.2rem;
  font-weight: 300;
}
.more-info-section {
  background: #fff;
  margin: 6% 8% 1% 7%;
  justify-content: center;
  align-items: center;
}
.hero-section > img {
  height: 100%;
  max-height: auto;
  width: 100%;
}
.card-box {
  width: 80%;
  box-shadow: 1px 1px 1px 1px #cdcdcd;
  font-family: 'Open Sans';
  font-size: 1rem;
}
.card-box > p {
  text-align: center;
  padding: 1% 8%;
}
.card-box > img {
  justify-self: center;
  align-self: center;
}
.bg-info-section {
  height: 35.1%;
  background: url("../images/banner02.jpg");
  background-size: 100% 100%;
}
.bg-info-section1 .image1 {
  width: 50%;
}
.bg-info-section1 .image2 {
  width: 50%;
}
.hero2 {
  height: 46.29%;
  background: url("../images/coffee_read_newspaper.jpg");
  background-size: 100% 100%;
}
.center-center {
  justify-content: center;
  align-items: center;
}
body {
  box-sizing: border-box;
  max-width: 100vw;
  max-width: 1920px;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 14px;
}
header {
  height: 15% !important;
  max-height: 15% !important;
}
#navigation {
  width: 100%;
  background: #fff;
}
@keyframes smoothScroll {
  0% {
    transform: translateY(-40px);
  }
  100% {
    transform: translateY(0px);
  }
}
.hero-bg-banner {
  background: url("../images/HomePageTopBanner.png");
  background-size: 100% 100%;
  height: 72.222%;
  min-height: 72.222%;
}
.fixed {
  position: fixed;
  transform: translateZ(0);
  top: 0;
  width: 100%;
  left: 0;
  z-index: 9994;
  height: 95px;
  background: seashell;
  animation: smoothScroll 1s forwards;
  color: #000;
  border-bottom: 2px solid #cdcdcd;
}
#footer {
  background: #343434;
}
#main-content {
  flex-grow: 1;
}
#logo-container {
  display: flex;
  max-width: 18%;
  padding: 2% 5.33%;
}
#logo-container > img {
  width: 235px;
  height: 40px;
}
#menu-container {
  flex-grow: 1;
  color: #343434;
  display: flex;
}
#menu-container > ul {
  list-style: none;
  flex-grow: 1;
  margin: 0;
  justify-content: flex-end;
  align-items: center;
  margin-right: 2%;
}
#menu-container > ul > li {
  padding: 1%;
  text-transform: uppercase;
  font-size: .9rem;
  font-family: 'Open Sans', sans-serif;
  margin-right: 2%;
}
#login-container {
  padding-right: 8.33%;
  justify-content: center;
  align-items: center;
}
#login-container > span {
  display: block;
  font-family: 'Open Sans', sans-serif;
  padding: 10px 32px;
  border: 1px solid #343434;
}
.fulliment-info-button {
  margin-top: 18%;
  margin-bottom: 4%;
  padding: 8% 25%;
  font-family: 'Segoe';
  background: #FFF;
  color: #000;
  text-transform: uppercase;
  box-shadow: 0px 0px 0px 2px #cdcdcd;
}
.sf-box-shadow {
  box-shadow: 0px 0px 0px 2px #cdcdcd;
}
